export function DrawerDemo() {
    return (
        <div className='flex flex-col items-start gap-4 md:flex-row md:items-center'>
            <DrawerBottom />
            <DrawerScrollableContent />
            <DrawerDirections />
        </div>
    );
}

function DrawerBottom() {
    const [goal, setGoal] = React.useState(350);

    const onClick = React.useCallback((adjustment: number) => {
        setGoal((prevGoal) => Math.max(200, Math.min(400, prevGoal + adjustment)));
    }, []);

    return (
        <Drawer>
            <DrawerTrigger asChild>
                <Button variant='outline'>Open Drawer</Button>
            </DrawerTrigger>
            <DrawerContent>
                <div className='mx-auto w-full max-w-sm'>
                    <DrawerHeader>
                        <DrawerTitle>Move Goal</DrawerTitle>
                        <DrawerDescription>Set your daily activity goal.</DrawerDescription>
                    </DrawerHeader>
                    <div className='p-4 pb-0'>
                        <div className='flex items-center justify-center space-x-2'>
                            <Button
                                variant='outline'
                                size='icon'
                                className='h-8 w-8 shrink-0 rounded-full'
                                onClick={() => onClick(-10)}
                                disabled={goal <= 200}>
                                <Minus />
                                <span className='sr-only'>Decrease</span>
                            </Button>
                            <div className='flex-1 text-center'>
                                <div className='text-7xl font-bold tracking-tighter'>{goal}</div>
                                <div className='text-muted-foreground text-[0.70rem] uppercase'>Calories/day</div>
                            </div>
                            <Button
                                variant='outline'
                                size='icon'
                                className='h-8 w-8 shrink-0 rounded-full'
                                onClick={() => onClick(10)}
                                disabled={goal >= 400}>
                                <Plus />
                                <span className='sr-only'>Increase</span>
                            </Button>
                        </div>
                        <div className='mt-3 h-[120px]'>
                            <ResponsiveContainer width='100%' height='100%'>
                                <BarChart data={data}>
                                    <Bar
                                        dataKey='goal'
                                        style={
                                            {
                                                fill: 'hsl(var(--foreground))',
                                                opacity: 0.9
                                            } as React.CSSProperties
                                        }
                                    />
                                </BarChart>
                            </ResponsiveContainer>
                        </div>
                    </div>
                    <DrawerFooter>
                        <Button>Submit</Button>
                        <DrawerClose asChild>
                            <Button variant='outline'>Cancel</Button>
                        </DrawerClose>
                    </DrawerFooter>
                </div>
            </DrawerContent>
        </Drawer>
    );
}

